import { React, useState } from "react";
import "./Quote.css";
import Card from "./Card";

const Quote = (props) => {
  const [randomIndex, setRandomIndex] = useState(
    Math.floor(Math.random() * 100 + 1)
  );
  const [randomColor, setRandomColor] = useState(
    Math.floor(Math.random() * 11 + 1)
  );
  function newQuoteHandler() {
    setRandomIndex(Math.floor(Math.random() * 100 + 1));
    setRandomColor(Math.floor(Math.random() * 11 + 1));
  }
  document.querySelector("body").style.background = props.colors[randomColor];

  return (
    <Card>
      <div
        className="quote"
        id="quote-box"
        style={{ color: props.colors[randomColor] }}
      >
        <i className="fas fa-quote-left" id="text" />
        {props.data[randomIndex].quote}
      </div>
      <div
        className="author"
        id="author"
        style={{ color: props.colors[randomColor] }}
      >
        - {props.data[randomIndex].author}
      </div>
      <div className="row">
        <div className="sc" style={{ background: props.colors[randomColor] }}>
          <a
            href="https://www.twitter.com/intent/tweet"
            title="Tweet this quote!"
            id="tweet-quote"
            target="_blank"
          >
            <i className="fab fa-twitter" />
          </a>
        </div>
        <div className="sc" style={{ background: props.colors[randomColor] }}>
          <a
            href="https://www.tumblr.com/share"
            title="Post this quote on tumblr!"
            id="tumblr-quote"
            target="_blank"
          >
            <i className="fab fa-tumblr" />
          </a>
        </div>
        <button
          className="newQuote"
          id="new-quote"
          onClick={newQuoteHandler}
          style={{ background: props.colors[randomColor] }}
        >
          New Quote
        </button>
      </div>
      <div className="attribution">
        Made by{" "}
        <a href="https://www.github.com/khoinpb96" target="_blank">
          khoinpb96
        </a>
      </div>
    </Card>
  );
};

export default Quote;
